/* üé® CSS Î≥ÄÏàò Ï†ïÏùò (ÎùºÏù¥Ìä∏/Îã§ÌÅ¨ ÌÖåÎßà) */
:root {
  --bg-primary: #f8fafc;
  --bg-secondary: #ffffff;
  --bg-tertiary: #f8fafc;
  --text-primary: #333333;
  --text-secondary: #1e293b;
  --text-muted: #64748b;
  --border-color: #e2e8f0;
  --border-hover: #3b82f6;
  --shadow-color: rgba(0,0,0,0.1);
  --modal-overlay: rgba(0,0,0,0.5);
  
  /* ÏûÖÎ†• ÌïÑÎìú Î∞∞Í≤Ω */
  --input-bg: #ffffff;
  --background-hover: #f1f5f9;
  --background-secondary: #ffffff;
  --primary-color-translucent: rgba(34, 197, 94, 0.1);
  
  /* ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉÅÌÉú ÏÉâÏÉÅ */
  --client-online: #fef3c7;
  --client-running: #dcfce7;
  --client-offline: #fef2f2;
  
  /* Î≤ÑÌäº ÏÉâÏÉÅ */
  --btn-primary: #22c55e;
  --btn-primary-hover: #16a34a;
  --btn-danger: #ef4444;
  --btn-danger-hover: #dc2626;
  --btn-secondary: #64748b;
  --btn-secondary-hover: #475569;
  
  /* Ï∂îÍ∞Ä ÏÉâÏÉÅ */
  --success-bg: #f0fdf4;
  --warning-bg: #fef3c7;
  --error-bg: #fef2f2;
  --info-bg: #f0f9ff;
}

/* üåô Îã§ÌÅ¨ ÌÖåÎßà */
[data-theme="dark"] {
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --text-primary: #f1f5f9;
  --text-secondary: #e2e8f0;
  --text-muted: #94a3b8;
  --border-color: #334155;
  --border-hover: #3b82f6;
  --shadow-color: rgba(0,0,0,0.3);
  --modal-overlay: rgba(0,0,0,0.7);
  
  /* Îã§ÌÅ¨ Î™®Îìú ÏûÖÎ†• ÌïÑÎìú Î∞∞Í≤Ω */
  --input-bg: #1e293b;
  --background-hover: #334155;
  --background-secondary: #1e293b;
  --primary-color-translucent: rgba(34, 197, 94, 0.2);
  
  /* Îã§ÌÅ¨ Î™®Îìú ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉÅÌÉú ÏÉâÏÉÅ */
  --client-online: #422006;
  --client-running: #14532d;
  --client-offline: #450a0a;
  
  /* Îã§ÌÅ¨ Î™®Îìú Ï∂îÍ∞Ä ÏÉâÏÉÅ */
  --success-bg: #14532d;
  --warning-bg: #422006;
  --error-bg: #450a0a;
  --info-bg: #1e3a8a;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: background-color 0.3s ease, color 0.3s ease;
}

.App {
  min-height: 100vh;
}

/* üé® Ìó§Îçî */
.header {
  background: linear-gradient(135deg, #4c1d95 0%, #6366f1 50%, #71717a 100%);
  color: white;
  padding: 12px 0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.header-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  font-size: 20px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
}

.status-info {
  display: flex;
  gap: 15px;
  font-size: 12px;
  align-items: center;
}

/* Socket Ïó∞Í≤∞ ÏÉÅÌÉú ÌëúÏãú */
.socket-status {
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

.socket-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #22c55e;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.socket-indicator.disconnected {
  background: #ef4444;
  animation: none;
}

.settings-btn {
  background: rgba(255,255,255,0.15);
  border: none;
  color: white;
  padding: 6px 8px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
}

.settings-btn:hover {
  background: rgba(255,255,255,0.25);
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

/* üé® ÌÜµÍ≥Ñ Î∞î */
.stats-bar {
  background: var(--bg-secondary);
  border-radius: 8px;
  padding: 25px 30px;
  margin-bottom: 20px;
  box-shadow: 0 1px 3px var(--shadow-color);
  transition: background-color 0.3s ease;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
  text-align: center;
}

.stat-item {
  padding: 5px;
}

.stat-value {
  font-size: 36px;
  font-weight: 700;
  color: #3b82f6;
  line-height: 1;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 12px;
  color: var(--text-muted);
  font-weight: 400;
}

/* üìã Î©îÏù∏ Î†àÏù¥ÏïÑÏõÉ */
.main-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 20px;
}

.section {
  background: var(--bg-secondary);
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 1px 3px var(--shadow-color);
  transition: background-color 0.3s ease;
}

.section-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 15px;
  color: var(--text-secondary);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* üîò Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.btn {
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
}

.btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 4px var(--shadow-color);
}

.btn-primary {
  background: var(--btn-primary);
  color: white;
}

.btn-primary:hover {
  background: var(--btn-primary-hover);
}

.btn-danger {
  background: var(--btn-danger);
  color: white;
}

.btn-danger:hover {
  background: var(--btn-danger-hover);
}

.btn-secondary {
  background: var(--btn-secondary);
  color: white;
}

.btn-secondary:hover {
  background: var(--btn-secondary-hover);
}

/* ÌÖçÏä§Ìä∏Í∞Ä ÏûàÎäî Î≤ÑÌäº */
.btn-with-text {
  width: auto;
  padding: 6px 12px;
  font-size: 12px;
  min-width: 80px;
  height: 28px;
}

/* ÏùºÍ¥Ñ Ï†úÏñ¥ Î≤ÑÌäº */
.btn-bulk {
  width: auto;
  padding: 4px 8px;
  font-size: 11px;
  min-width: 60px;
  height: 26px;
}

/* üìù ÌîÑÎ¶¨ÏÖã Ïπ¥Îìú */
.preset-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.preset-card {
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 15px;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--bg-secondary);
  position: relative;
}

.preset-card:hover {
  border-color: var(--border-hover);
  background: var(--bg-tertiary);
}

.preset-card.active {
  border-color: #22c55e;
  background: var(--success-bg);
}

.preset-checkbox {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 16px;
  height: 16px;
  z-index: 1;
}

.preset-content {
  flex: 1;
  margin-left: 24px;
}

.preset-name {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 4px;
}

.preset-info {
  font-size: 11px;
  color: var(--text-muted);
  line-height: 1.3;
}

.preset-actions {
  display: flex;
  gap: 4px;
  flex-shrink: 0;
}

/* üë• Í∑∏Î£π Ïπ¥Îìú */
.group-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.group-card {
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 15px;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--bg-secondary);
  position: relative;
}

.group-card:hover {
  border-color: var(--border-hover);
  background: var(--bg-tertiary);
}

.group-checkbox {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 16px;
  height: 16px;
  z-index: 1;
}

.group-content {
  flex: 1;
  margin-left: 24px;
}

.group-name {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 4px;
}

.group-info {
  font-size: 11px;
  color: var(--text-muted);
  margin-bottom: 8px;
}

.group-clients {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.group-actions {
  display: flex;
  gap: 4px;
  flex-shrink: 0;
  align-items: center;
}

.client-tag {
  background: #22c55e;
  color: white;
  padding: 2px 6px;
  border-radius: 10px;
  font-size: 10px;
  font-weight: 500;
}

/* ÏùºÍ¥Ñ Ï†úÏñ¥ UI */
.bulk-controls {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.3s ease;
}

.selection-info {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 12px;
  color: var(--text-muted);
}

.bulk-actions {
  display: flex;
  gap: 6px;
}

/* üñ•Ô∏è ÎîîÏä§ÌîåÎ†àÏù¥ ÏÑúÎ≤Ñ Î™®ÎãàÌÑ∞ÎßÅ */
.client-monitor {
  background: var(--bg-secondary);
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 1px 3px var(--shadow-color);
  transition: background-color 0.3s ease;
}

.monitor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.client-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: 12px;
}

.client-item {
  background: var(--client-online);
  border-radius: 6px;
  padding: 12px 8px;
  text-align: center;
  transition: all 0.2s ease;
  cursor: pointer;
  position: relative;
  border: 2px solid transparent;
}

.client-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px var(--shadow-color);
}

.client-item.online {
  background: var(--client-online);
  border-color: #f59e0b;
}

.client-item.running {
  background: var(--client-running);
  border-color: #22c55e;
}

.client-item.offline {
  background: var(--client-offline);
  border-color: #ef4444;
  opacity: 0.7;
}

.client-icon {
  font-size: 24px;
  margin-bottom: 6px;
}

.client-name {
  font-weight: 600;
  font-size: 12px;
  margin-bottom: 2px;
  color: var(--text-secondary);
}

.client-ip {
  font-size: 10px;
  color: var(--text-muted);
  margin-bottom: 4px;
}

.client-status {
  font-size: 9px;
  color: var(--text-muted);
  font-weight: 500;
  margin-bottom: 4px;
}

/* ÏÑ±Îä• Î©îÌä∏Î¶≠ ÌëúÏãú */
.client-metrics {
  margin-top: 4px;
  padding-top: 4px;
  border-top: 1px solid var(--border-color);
  font-size: 9px;
  color: var(--text-muted);
  line-height: 1.2;
}

.client-metrics.visible {
  display: block;
}

/* Ïã§Ìñâ ID ÌëúÏãú */
.execution-id {
  position: absolute;
  top: 4px;
  right: 4px;
  background: rgba(0,0,0,0.7);
  color: white;
  padding: 2px 4px;
  border-radius: 4px;
  font-size: 8px;
  font-weight: 600;
}

/* üìä Ïã§Ìñâ Î™®ÎãàÌÑ∞ÎßÅ ÏÑπÏÖò */
.execution-monitor {
  background: var(--bg-secondary);
  border-radius: 8px;
  padding: 20px;
  margin-top: 20px;
  box-shadow: 0 1px 3px var(--shadow-color);
  transition: background-color 0.3s ease;
}

.execution-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 400px;
  overflow-y: auto;
}

.execution-item {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 12px;
  transition: all 0.2s ease;
}

.execution-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.execution-details {
  font-size: 11px;
  color: var(--text-muted);
  margin-bottom: 8px;
}

.execution-targets {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-bottom: 8px;
}

.target-client {
  padding: 2px 6px;
  border-radius: 10px;
  font-size: 9px;
  font-weight: 500;
}

.target-client.success {
  background: var(--success-bg);
  color: #14532d;
}

.target-client.failed {
  background: var(--error-bg);
  color: #991b1b;
}

.target-client.pending {
  background: var(--warning-bg);
  color: #92400e;
}

.execution-status {
  font-size: 10px;
  padding: 2px 8px;
  border-radius: 12px;
  font-weight: 600;
}

.execution-status.pending {
  background: var(--warning-bg);
  color: #92400e;
}

.execution-status.running {
  background: var(--info-bg);
  color: #1e40af;
}

.execution-status.completed {
  background: var(--success-bg);
  color: #14532d;
}

.execution-status.failed {
  background: var(--error-bg);
  color: #991b1b;
}

.execution-progress {
  height: 4px;
  background: var(--border-color);
  border-radius: 2px;
  overflow: hidden;
  margin-top: 8px;
}

.execution-progress-bar {
  height: 100%;
  background: #3b82f6;
  transition: width 0.3s ease;
}

/* Î™®Îã¨ Ïä§ÌÉÄÏùº */
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--modal-overlay);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: var(--bg-secondary);
  border-radius: 8px;
  padding: 20px;
  max-width: 600px;  /* Îçî ÎÑìÍ≤å */
  width: 90%;
  max-height: 80%;
  overflow-y: auto;
  animation: modalSlideIn 0.3s ease;
  transition: background-color 0.3s ease;
}

@keyframes modalSlideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--border-color);
}

.modal-header h3 {
  color: var(--text-secondary);
}

.close {
  font-size: 24px;
  cursor: pointer;
  color: var(--text-muted);
}

.close:hover {
  color: #ef4444;
}

/* Ï†ïÎ≥¥ ÏÑπÏÖò */
.info-section {
  margin-bottom: 20px;
}

.info-section h4 {
  font-size: 14px;
  margin-bottom: 10px;
  color: var(--text-secondary);
}

.info-grid {
  display: grid;
  gap: 8px;
  font-size: 12px;
  color: var(--text-muted);
}

/* Î≤ÑÌäº Í∑∏Î£π */
.button-group {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

/* ÏúÑÌóò Íµ¨Ïó≠ Ïä§ÌÉÄÏùº */
.danger-section {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid #fee2e2;
}

.warning-text {
  font-size: 11px;
  color: #991b1b;
  margin-top: 5px;
}

/* Î∞òÏùëÌòï ÎîîÏûêÏù∏ */
@media (max-width: 1024px) {
  .main-layout {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  
  .preset-grid,
  .group-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    gap: 8px;
    text-align: center;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
  
  .section {
    padding: 15px;
  }

  .bulk-controls {
    flex-direction: column;
    gap: 10px;
  }
  
  .preset-grid,
  .group-grid {
    grid-template-columns: 1fr;
  }

  .client-grid {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }
}

/* Ìèº Ïä§ÌÉÄÏùº */
.form-group {
  margin-bottom: 16px;
}

.form-group label {
  display: block;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 6px;
}

.form-input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  font-size: 13px;
  font-family: inherit;
  transition: border-color 0.2s;
  background: var(--bg-secondary);
  color: var(--text-primary);
}

.form-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

textarea.form-input {
  resize: vertical;
  min-height: 60px;
}

.form-help {
  display: block;
  font-size: 11px;
  color: var(--text-muted);
  margin-top: 4px;
}

/* ÎùºÎîîÏò§ Î≤ÑÌäº Í∑∏Î£π (ÏÉòÌîåÍ≥º ÎèôÏùºÌïòÍ≤å) */
.radio-group {
  display: grid;
  gap: 8px;
  max-height: 200px;
  overflow-y: auto;
  padding: 8px;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  transition: background-color 0.3s ease;
}

.radio-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  cursor: pointer;
  padding: 8px;
  border-radius: 4px;
  transition: background 0.2s;
  color: var(--text-primary);
  border: 1px solid transparent;
}

.radio-label:hover {
  background: var(--border-color);
}

.radio-label.selected {
  background: var(--info-bg);
  border-color: #3b82f6;
}

.radio-label input[type="radio"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

/* ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏Î≥Ñ Î™ÖÎ†πÏñ¥ ÏûÖÎ†• Ïä§ÌÉÄÏùº */
.client-command-section {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 15px;
  margin-top: 15px;
  transition: background-color 0.3s ease;
}

.client-command-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--border-color);
}

.client-command-item {
  margin-bottom: 15px;
  padding: 12px;
  background: var(--bg-secondary);
  border-radius: 6px;
  border: 1px solid var(--border-color);
}

.client-command-item label {
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 8px;
  display: block;
}

.client-command-item .form-input {
  font-family: 'Courier New', monospace;
  font-size: 12px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 24px;
  padding-top: 16px;
  border-top: 1px solid var(--border-color);
}

/* ÌÜ†Ïä§Ìä∏ */
.toast {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: var(--text-secondary);
  color: white;
  padding: 12px 16px;
  border-radius: 6px;
  display: none;
  animation: slideIn 0.3s ease;
  box-shadow: 0 4px 12px var(--shadow-color);
  font-size: 12px;
  transition: background-color 0.3s ease;
  max-width: 300px;
  z-index: 1001;
}

.toast.success {
  background: #22c55e;
}

.toast.error {
  background: #ef4444;
}

.toast.warning {
  background: #f59e0b;
}

.toast.info {
  background: #3b82f6;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

/* ÏÉÅÌÉú Î±ÉÏßÄ */
.status-badge {
  display: inline-block;
  padding: 2px 8px;
  font-size: 10px;
  font-weight: 600;
  border-radius: 12px;
  text-transform: uppercase;
}

.status-badge.online {
  background: var(--success-bg);
  color: #14532d;
}

.status-badge.offline {
  background: var(--error-bg);
  color: #991b1b;
}

.status-badge.running {
  background: var(--info-bg);
  color: #1e40af;
}

/* API Ïó∞Í≤∞ ÏÉÅÌÉú ÌëúÏãú */
.api-status {
  position: fixed;
  top: 70px;
  right: 20px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 8px 12px;
  font-size: 11px;
  box-shadow: 0 2px 8px var(--shadow-color);
  z-index: 999;
  display: none;
}

.api-status.visible {
  display: block;
}

.api-indicator {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 6px;
}

.api-indicator.connected {
  background: #22c55e;
}

.api-indicator.disconnected {
  background: #ef4444;
}
